@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a0a0a;
  color: white;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

#root {
  min-height: 100vh;
  width: 100%;
}

/* Touch optimization for mobile */
.touch-manipulation {
  touch-action: manipulation;
}

/* Safe area for mobile devices */
.safe-area-inset-bottom {
  padding-bottom: env(safe-area-inset-bottom);
}

/* Custom Scrollbars - Kimi V2 theme */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #6b46c1 rgba(255, 255, 255, 0.06);
}

/* WebKit (Chrome, Edge, Safari) */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.06);
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #6b46c1 0%, #3b82f6 100%);
  border-radius: 8px;
  border: 2px solid rgba(10, 10, 10, 0.9);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #7c59d1 0%, #4f8ef7 100%);
}

html, body {
  scrollbar-gutter: stable;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in {
  animation: fadeIn 0.3s ease-out;
}

.animate-fade-in-delayed {
  animation: fadeIn 1s ease-out 3s both;
}

/* Hide scrollbar while keeping scroll functionality */
.scrollbar-hide {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.scrollbar-hide::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}
